import React, { useCallback, useEffect } from "react";
import { Button } from "../Button/Button";
import { Modal } from "../Modal/Modal";
import "./AddTaskModal.css";

export interface IAddTaskModalProps {
    open: boolean;
    setOpen: any;
    onCancel: any;
    addItem: any;
    inputValue: any;
    setInputValue: any;
}

export const AddTaskModal = (props: IAddTaskModalProps) => {
    const {open, setOpen, onCancel, addItem, inputValue, setInputValue} = props;
    const buttons = [
        <Button text={"Отменить"} onClick={onCancel}></Button>,
        <Button text={"Сохранить"} className={"add-task-button"}  onClick={addItem}></Button>
      ]
    let textInput = null as any;

    const resize = () => {
        var str = textInput.value;
        var cols = textInput.cols + 2;
        if(str !== 2) {
            var linecount = 0;
            str && str.split("\n").forEach((line: any) => {
                linecount += Math.ceil( line.length / cols );
            }); 
            textInput.rows = linecount + 1;
        }
    }  

    useEffect(() => {
        if(open) {
            textInput.rows = 2
        }
    }, [open, textInput])

    
    return (
        <Modal open={open} setOpen={setOpen} buttons={buttons} onCancel={onCancel}>
            <div className={"modal-content"}>
                <div>{"Введите название задачи"}</div>
                <textarea ref={el => textInput = el} rows={2} cols={35} value={inputValue} onKeyUp={resize} onChange={(e) => setInputValue(e.target.value)}></textarea>
            </div>
        </Modal>)
}